<template>
   <el-table 
    :data="categoryData"
    >
    <el-table-column
      label="分类名称"
    >
    <template slot-scope="scope" >
        <router-link :to="{ name: 'category', params: { id: scope.row.id }}">{{scope.row.name}}</router-link>
      </template>
    </el-table-column>
     <el-table-column
      prop="review_user_count"
      label="用户评论次数"
      >
    </el-table-column>
    </el-table>
</template>

<script>
export default {
  props: {
    categoryData: {
      type: Array
    }
  }
}
</script>




