import request from '@/utils/request'

export function getUser (page, size) {
  return request({
    url: '/user',
    method: 'get',
    params: { page, size }
  })
}

export function getUserCategory (id, page, size) {
  return request({
    url: '/user/' + id,
    method: 'get',
    params: { page, size }
  })
}



