import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home'
import User from '@/views/user'
import Category from '@/views/category'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    { path: '/user/:id',
      name: 'user',
      component: User
    },
    { path: '/category/:id',
      name: 'category',
      component: Category
    }
  ]
})



