<template>
 <el-container class="container">
  <el-header >
    <el-menu :default-active="activeIndex" 
    mode="horizontal" 
    @select="handleSelect"
    background-color="#545c64"
    active-text-color="#ffd04b"
    text-color="#fff">
      <el-menu-item index="1">用户</el-menu-item>
      <el-menu-item index="2">分类</el-menu-item>
    </el-menu>
  </el-header>
  <el-main class="content">
    <category-list 
    v-show="activeIndex==2"
    :categoryData="categoryData"
    >
    </category-list>
  
  <user-list v-show="activeIndex==1" :tableData="tableData"></user-list>
  
  </el-main>
    <el-footer>

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-sizes="[15, 20,25,30]"
      :page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  
    </el-footer>
 </el-container>
</template>

<script>
import { getUser } from '@/api/user'
import { getCategory } from '@/api/category'
import userList from '@/components/user'
import categoryList from '@/components/category'
export default {
  name: 'home',

  components: {
    userList,
    categoryList
  },

  created: function () {
    this.Getusers(this.page, this.size)
  },
  data () {
    return {
      activeIndex: '1',
      total: 0,
      page: 1,
      size: 15,
      categoryData: [],
      tableData: []
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      this.activeIndex = key

      this.loadData()
    },
    loadData () {
      switch (this.activeIndex) {
        case '1':
          this.Getusers(this.page, this.size)
          break
        case '2':
          this.Getcateogry(this.page, this.size)
          break
        default:
          break
      }
    },

    handleSizeChange (val) {
      this.size = val
    },

    handleCurrentChange (val) {
      this.page = val
      this.loadData()
    },

    Getcateogry (page, size) {
      return new Promise((resolve, reject) => {
        getCategory(page, size)
          .then(response => {
            this.categoryData = response.data.datas
            this.total = response.data.total
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    },

    Getusers (page, size) {
      return new Promise((resolve, reject) => {
        getUser(page, size)
          .then(response => {
            this.tableData = response.data.datas
            this.total = response.data.total
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    }
  }
}
</script>

<style >
.el-footer {

  align-self: center;
}

.el-menu {
  border-bottom: solid 0;
}

.el-header {
  padding: 5;
  background-color: #545c64;
}

.content {
  background-color: black;
  padding: 0;
}

.container {
  height: 500;
  width: 100%;
}
</style>



