<template>
    <el-table
    :data="tableData"
    style="width: 100%"
    border
    >
  <el-table-column
      label="昵称"
      >
      <template slot-scope="scope" >
        <router-link :to="{ name: 'user', params: { id: scope.row.id }}">{{scope.row.name}}</router-link>
      </template>
    </el-table-column>
    <el-table-column
      label="邮箱"
      prop="email"
      >
    </el-table-column>
    
    <el-table-column
      label="Facebook"
      
      >
      <template slot-scope="scope">
        <a style="text-decoration: none" target="_blank" :href="scope.row.facebook"> {{  formatter(scope.row,{label:'Facebook'})}}</a>
      </template>
    </el-table-column>
     <el-table-column
      label="Twitter"
     
      >
      <template slot-scope="scope">
        <a style="text-decoration: none;" target="_blank" :href="scope.row.twitter"> {{  formatter(scope.row,{label:'Twitter'})}}</a>
      </template>
    </el-table-column>
     <el-table-column
      label="Instagram"
     
      >
      <template slot-scope="scope">
        <a style="text-decoration: none;" target="_blank" :href="scope.row.instagram"> {{  formatter(scope.row,{label:'Instagram'})}}</a>
      </template>
    </el-table-column>
     <el-table-column
      label="Pinterest"
     
      >
        <template slot-scope="scope">
        <a style="text-decoration: none;" target="_blank"  :href="scope.row.pinterest"> {{  formatter(scope.row,{label:'Pinterest'})}}</a>
        </template>
    </el-table-column>
    <el-table-column
      label="Youtube"
     
      >
      <template slot-scope="scope">
        <a style="text-decoration: none;" target="_blank" :href="scope.row.youtube"> {{  formatter(scope.row,{label:'Youtube'})}}</a>
      </template>
    </el-table-column>
    <el-table-column
      prop="review_count"
      label="评论总数"
     
      >
    </el-table-column>
  </el-table>  
</template>

<script>
export default {
  data () {
    return {

    }
  },
  props: {
    tableData: {
      type: Array

    }
  },
  methods: {
    formatter (row, column) {
      switch (column.label) {
        case 'Facebook':
          return row.facebook.substring(25, row.facebook.lenght)
        case 'Twitter':
          return row.twitter.substring(25, row.twitter.lenght)
        case 'Instagram':
          return row.instagram
            .substring(26, row.instagram.lenght)
            .replace('/', '')
        case 'Pinterest':
          return row.pinterest
            .substring(26, row.pinterest.lenght)
            .replace('/', '')
        case 'Youtube':
          return row.youtube.substring(26, row.youtube.lenght)
        default:
          break
      }
    }
  }
}
</script>



