<template>
<div>
  <div class="header"></div>
  
<el-table :data="tableData"
          border>
    <el-table-column 
    label="分类"
    prop="name">
    </el-table-column>
     <el-table-column 
     label="评论次数"
     prop="review_count">
    </el-table-column>
  </el-table>
   <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-sizes="[15, 20,25,30]"
      :page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
</div>
  
</template>

<script>
import { getUserCategory } from '@/api/user'

export default {
  data () {
    return {
      tableData: [],
      page: 0,
      size: 15
    }
  },
  created () {
    this.loadData()
  },
  methods: {
    loadData () {
      this.GetUserCategory(this.$route.params.id, this.page, this.size)
    },

    GetUserCategory (id, page, size) {
      return new Promise((resolve, reject) => {
        getUserCategory(id, page, size)
          .then(response => {
            this.tableData = response.data.datas
            this.total = response.data.total
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    },

    handleSizeChange (val) {
      this.size = val
    },

    handleCurrentChange (val) {
      this.page = val
      this.loadData()
    }
  }
}
</script>

<style>
.el-pagination {
  padding: 40px;
  align-self: center;
}
.header{
  height: 61px;
  width: 100%;
  background-color:#4A5159
}
</style>





